@import url('https://fonts.googleapis.com/css2?family=VT323&display=swap');

:root {
  --retro-green: #00ff00;
  --retro-green-dark: #00cc00;
  --retro-bg: #1d1f24;
  --retro-text: #dcdcdc;
  --retro-glow: rgba(0, 255, 0, 0.5);
  --retro-glow-light: rgba(0, 255, 0, 0.3);
  --retro-glow-dark: rgba(0, 255, 0, 0.1);
}

body {
  margin: 0;
  background-color: var(--retro-bg);
  color: var(--retro-text);
  font-family: 'VT323', monospace;
  overflow-x: hidden;
  line-height: 1.6;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
              repeating-linear-gradient(0deg, transparent, transparent 1px, var(--retro-glow-dark) 1px, var(--retro-glow-dark) 2px);
}

.App {
  display: flex;
  flex-direction: column;
  overflow: visible;
  min-height: 100vh;
}

.page {
  display: flex;
  flex-direction: row;
  position: relative;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
              repeating-linear-gradient(0deg, transparent, transparent 1px, var(--retro-glow-dark) 1px, var(--retro-glow-dark) 2px);
}

.App-header {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: var(--retro-green);
  font-size: clamp(2rem, 5vw, 4rem);
  text-align: center;
  flex-direction: column;
  padding: 0 20px;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
              repeating-linear-gradient(0deg, transparent, transparent 1px, var(--retro-glow-dark) 1px, var(--retro-glow-dark) 2px);
}

.App-header h1 {
  font-weight: 700;
  margin: 0;
  position: relative;
  overflow: hidden;
  border-right: 2px solid var(--retro-green);
  white-space: nowrap;
  animation: typing 3.5s steps(40, end),
             blink-caret 0.75s step-end infinite;
}

.App-header p {
  font-size: clamp(1rem, 2.5vw, 2rem);
  color: var(--retro-green);
  opacity: 0;
  animation: fadeIn 2s ease-in forwards;
  animation-delay: 3.5s;
}

@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: var(--retro-green) }
}

@keyframes fadeIn {
  from { opacity: 0 }
  to { opacity: 1 }
}

.title{
  margin-bottom: 10vh;
  font-size:xx-large;

}

@keyframes reveal {
  from {
    width: 100%;
  }
  to {
    width: 0%; 
  }
}


.special{
  font-size: larger;
  text-align: center;
  letter-spacing: .30em; 
  margin-bottom: 10vh;

}
.special.Contact{
  margin-bottom: 0;
}

.special p{

  width: 90%;
}

h1, p{

  margin: 0 auto; 
  letter-spacing: .15em; 

}


@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

.fade-in {
  opacity: 0;
  transform: translateY(-20px);
}

.fade-in.visible {
  animation: fadeIn 1s ease-out forwards;
}

@keyframes fadeIn {
  0% { opacity: 0; transform: translateY(-20px); }
  100% { opacity: 1; transform: translateY(0); }
}


.fade-in {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 1s ease-out, transform 1s ease-out;
}

.fade-in.visible {
  opacity: 1;
  transform: translateY(0);
}




.side-menu {
  top: 0;
  position: fixed;
  right: -10vw;
  width: 10vw;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  z-index: 1000;
  transform: translateX(0);
  opacity: 0;
  transition: transform 0.3s ease, opacity 0.3s ease;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
              repeating-linear-gradient(0deg, transparent, transparent 1px, var(--retro-glow-dark) 1px, var(--retro-glow-dark) 2px);
  backdrop-filter: blur(10px);
  border-left: 1px solid var(--retro-green);
}

.side-menu.visible {
  transform: translateX(-10vw);
  opacity: 1;
}

.side-menu.hidden {
  transform: translateX(0);
  opacity: 0;
}

.menu-button {
  background: var(--retro-glow-dark);
  border: 1px solid var(--retro-green);
  color: var(--retro-green);
  font-size: clamp(0.8rem, 1.2vw, 1.2rem);
  cursor: pointer;
  text-align: center;
  padding: 0.8rem 1.2rem;
  transition: all 0.3s ease;
  border-radius: 8px;
  width: 80%;
  text-shadow: 0 0 5px var(--retro-glow);
  font-family: 'VT323', monospace;
}

.button{
  letter-spacing: .30em; 
}
.menu-button.active-button {
  background: var(--retro-glow-light);
  color: var(--retro-green);
  border: 1px solid var(--retro-green);
  transform: scale(1.05);
  box-shadow: 0 0 10px var(--retro-glow);
}

.menu-button:hover {
  background: var(--retro-glow-light);
  box-shadow: 0 0 10px var(--retro-glow);
}

.contents {
  display: flex;
  flex-direction: column;
  width: calc(100vw - 10vw);
  padding: 2rem;
  align-items: center;
  margin-right: 10vw;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
              repeating-linear-gradient(0deg, transparent, transparent 1px, var(--retro-glow-dark) 1px, var(--retro-glow-dark) 2px);
}

/* content Boxes */
.content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15vh;
  width: 100%;
  max-width: 1200px;
  gap: 4rem;
  padding: 2rem;
  border: 1px solid var(--retro-glow-dark);
  box-shadow: 0 0 10px var(--retro-glow-light);
}

.content:hover {
  border-color: var(--retro-green);
  box-shadow: 0 0 15px var(--retro-glow);
}

.content.left {
  flex-direction: row;
}

.content.right {
  flex-direction: row-reverse;
}

.content-content {
  flex: 1;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.content-content.left {
  text-align: left;
  align-items: flex-start;
}

.content-content.right {
  text-align: right;
  align-items: flex-end;
}

.content-content h2 {
  font-size: clamp(1.5rem, 2.5vw, 2.5rem);
  margin-bottom: 1rem;
  color: var(--retro-green);
  font-weight: 600;
  text-shadow: 0 0 5px var(--retro-glow);
}

.content-content p {
  font-size: clamp(1rem, 1.2vw, 1.2rem);
  line-height: 1.8;
  margin-bottom: 1.5rem;
  color: var(--retro-text);
}

.content-content a {
  display: inline-block;
  padding: 0.8rem 1.5rem;
  background: var(--retro-glow-dark);
  color: var(--retro-green);
  border: 1px solid var(--retro-green);
  border-radius: 4px;
  text-decoration: none;
  transition: all 0.3s ease;
  text-shadow: 0 0 5px var(--retro-glow);
  font-family: 'VT323', monospace;
}

.content-content a:hover {
  background: var(--retro-glow-light);
  box-shadow: 0 0 10px var(--retro-glow);
}

.content-image {
  flex: 1;
  max-width: 500px;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 0 10px var(--retro-glow);
  border: 1px solid var(--retro-glow-dark);
}

.content-image img {
  width: 100%;
  height: auto;
  object-fit: cover;
  transition: transform 0.3s ease;
  filter: grayscale(20%) contrast(110%);
}

.content-image img:hover {
  transform: scale(1.05);
  filter: grayscale(0%) contrast(120%);
}

.show-menu-button {
  position: fixed;
  top: 2rem;
  right: 2rem;
  background: var(--retro-glow-dark);
  border: 1px solid var(--retro-green);
  color: var(--retro-green);
  padding: 0.8rem;
  border-radius: 8px;
  cursor: pointer;
  z-index: 1001;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  box-shadow: 0 0 10px var(--retro-glow);
}

.show-menu-button:hover {
  background: var(--retro-glow-light);
  transform: scale(1.1);
}

.content.special {
  flex-direction: column; 
  
  margin-bottom: 20vh;
} 


.content-content.special{
 
  justify-content: center;
  text-align: center;
  max-width: 50%;
}

.content-content.special p,.content-content.special h1{
  text-align: center;
  align-items: center;
}

.title {
  text-align: center;
  margin: 5vh 0;
  width: 100%;
}

.title h1 {
  font-size: clamp(2rem, 4vw, 3rem);
  color: var(--retro-green);
  margin: 0;
  text-shadow: 0 0 5px var(--retro-glow);
}

@media (max-width: 700px) {
  .content {
    flex-direction: column;
    margin-bottom: 8vh;
    padding: 1rem;
    gap: 2rem;
    align-items: center;
  }

  .content.left,
  .content.right {
    flex-direction: column;
  }

  .content-content {
    width: 100%;
    text-align: center;
    align-items: center;
    order: 2;
  }

  .content-content.left,
  .content-content.right {
    text-align: center;
    align-items: center;
  }

  .content-content h2 {
    text-align: center;
    width: 100%;
  }

  .content-content p {
    text-align: center;
    width: 100%;
  }

  .content-image {
    max-width: 100%;
    order: 1;
  }

  .button-container {
    width: 100%;
    display: flex;
    justify-content: center;
  }

  .content-content a {
    width: auto;
    text-align: center;
  }

  .title {
    margin: 3vh 0;
  }

  .title h1 {
    font-size: 2rem;
  }

  .menu-button {
    width: 90%;
    font-size: 1.2rem;
    padding: 1rem;
  }

  .show-menu-button {
    top: 1rem;
    right: 1rem;
  }

  .side-menu {
    width: 100vw;
    right: -100vw;
    background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
                repeating-linear-gradient(0deg, transparent, transparent 1px, var(--retro-glow-dark) 1px, var(--retro-glow-dark) 2px);
    border-left: 1px solid var(--retro-green);
  }

  .side-menu.visible {
    transform: translateX(-100vw);
  }
}



logo {
  display: inline-flex; /* Flex for alignment */
  align-items: center;
  justify-content: center;
  text-decoration: none; /* Remove underline for links */
  color: inherit; /* Inherit color from parent, can override later */
  font-size: 5rem; /* Adjust icon size */
}

/* Hover effect for the logo */
logo:hover {
  transform: scale(1.1); /* Slightly enlarge on hover */
  color: #61dafb; /* Change to React logo color on hover (optional) */
}

